"use client";
import { useState } from "react";

export default function Sidebar() {
  const [isSidebarOpen, setSidebarOpen] = useState(false);
  const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);

  return (
    <div className="relative flex">
      {/* Sidebar for Medium and Larger Screens */}
      <div
        className={`bg-white shadow-lg transform transition-all duration-300 ${
          isSidebarOpen ? "w-64" : "w-16"
        } hidden md:block`} // This hides the sidebar for mobile screens
      >
        <div className="h-full flex flex-col items-center md:items-start p-4">
          {/* Sidebar Content */}
          <ul className={`space-y-4 w-full ${isSidebarOpen ? "pl-2" : "flex flex-col items-center"}`}>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Dashboard</span>}
            </li>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Analytics</span>}
            </li>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Projects</span>}
            </li>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Team</span>}
            </li>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Settings</span>}
            </li>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Help</span>}
            </li>
            <li className="flex items-center space-x-2">
              {isSidebarOpen && <span className="text-gray-700">Logout</span>}
            </li>
          </ul>
        </div>
      </div>

      {/* Mobile Menu (Only visible on small screens) */}
      <div
        className={`fixed inset-0 bg-black bg-opacity-50 md:hidden ${
          isMobileMenuOpen ? "block" : "hidden"
        }`}
      >
        <div className="bg-white p-6 space-y-4">
          <button
            onClick={() => setMobileMenuOpen(false)}
            className="text-gray-600 hover:text-gray-900 mb-4"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
          {/* Mobile Menu Content */}
          <ul className="space-y-4">
            <li>
              <span className="block text-gray-700">Dashboard</span>
            </li>
            <li>
              <span className="block text-gray-700">Analytics</span>
            </li>
            <li>
              <span className="block text-gray-700">Projects</span>
            </li>
            <li>
              <span className="block text-gray-700">Team</span>
            </li>
            <li>
              <span className="block text-gray-700">Settings</span>
            </li>
            <li>
              <span className="block text-gray-700">Help</span>
            </li>
            <li>
              <span className="block text-gray-700">Logout</span>
            </li>
          </ul>
        </div>
      </div>

      {/* Menu Icon for Small Screens */}
      <button
        onClick={() => setMobileMenuOpen(!isMobileMenuOpen)}
        className="absolute top-4 left-4 text-gray-600 hover:text-gray-900 md:hidden z-10"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16m-7 6h7"
          />
        </svg>
      </button>

      {/* Large Screen Sidebar Toggle Button */}
      <button
        onClick={() => setSidebarOpen(!isSidebarOpen)}
        className="hidden md:block text-gray-600 hover:text-gray-900 absolute top-4 right-4 z-10"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16m-7 6h7"
          />
        </svg>
      </button>
    </div>
  );
}
